*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Montserrat', sans-serif;
}

.proyectos{
    display: flex;
    flex-direction: column;
    margin: 50px 0;
    text-align: center;
    font-size: 20px;
    align-items: center;
}

.resaltar{
    font-weight: 800;
    font-size: 30px;
    font-style: italic;
}

.texto-proyectos{
    width: 80%;
    padding-top: 20px;
}

.nosotros{
    display: flex;
    flex-direction: column;
    background: #465362;
    padding-bottom: 20px;
}
.titulo{
    text-align: center;
    font-size: 30px;
    font-weight: 500;
    margin: 20px 0;
}
.titulo-nosotros{
    color: #ffff;
}

.contenedores-nosotros{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.contenedor-nosotros{
    padding: 0 10px;
    color: #ffff;
    width: 30%;
    height: auto;
}

.contenedor-nosotros h3{
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 10px;
}

.contenedor-svg{
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
}

.contenedor-nosotros p{
    text-align: justify;
    font-size: 15px;
    line-height: 25px;
}

/*SERVICIOS*/

.contenedores-servicios{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.contenedor-servicio{
    width: 220px;
    height: 220px;
    background: #4AC03F;
    border-radius: 50%;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    background-position: center;
    background-size: cover;
    box-shadow:  -4px 3px 13px 0px rgba(0, 0, 0, 0.59);
    cursor: pointer;
}

.contenedor-servicio h3{
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    margin: 20px;
    color: #ffff;
}

.contenedor-servicio p{
    text-align: center;
    font-size: 15px;
    line-height: 25px;  
    padding: 0 30px;  
}

.hover-servicio{
    position: absolute;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    top: 0;
    transform: scale(0);
    background: #37992e;
    transition: transform .5s;
    color: #ffff;
    display: flex;
    align-items: center;
}

.contenedor-servicio:hover .hover-servicio{
    transform: scale(1);
}

.instalacion{
    background-image: linear-gradient(rgba(0, 0, 0, 0.493), rgba(0, 0, 0, 0.534)), url('./instalacion.jpg');
}

.mantenimiento{
    background-image: linear-gradient(rgba(0, 0, 0, 0.493), rgba(0, 0, 0, 0.534)), url('./mantenimiento.jpg');
}

.bombeo{
    background-image: linear-gradient(rgba(0, 0, 0, 0.493), rgba(0, 0, 0, 0.534)), url('./bombeo.jpg');
}

.design{
    background-image: linear-gradient(rgba(0, 0, 0, 0.493), rgba(0, 0, 0, 0.534)), url('./design.jpeg');
}

/*PORTAFOLIO*/

.contenedores-portafolio{
    width: 100%;
    height: 500px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "primero segundo tercero"
                        "primero cuarto cuarto";
}

.primero{
    grid-area: primero;
    background: rgb(32, 158, 180);
}

.segundo{
    grid-area: segundo;
    background: rgb(24, 24, 138);
}

.tercero{
    grid-area: tercero;
    background: rgb(40, 171, 189);
}

.cuarto{
    grid-area: cuarto;
    background: rgb(16, 34, 94);
}